<template>
	<div>
		<div>
			<div class="s-toast">
				<rm-toast :type="type" v-model="show">{{ message }}</rm-toast>
				<div class="vui-title"></div>
				<div class="vui-button" @click="toast('success', '')">success</div>
				<div class="vui-title"></div>
				<div class="vui-button" @click="toast('cancel', '')">cancel</div>
				<div class="vui-title"></div>
				<div class="vui-button" @click="toast('text', '')">text</div>
				<div class="vui-title"></div>
				<div class="vui-button" @click="toastpublin('')">text</div>
				<p></p>
				<div class="vui-button" @click="toastpublin('success')">success</div>
			</div>
		</div>
	</div>
</template>

<script>

export default {
	path: 'toast',
	data() {
		return {
			show: false,
			type: '',
			message: '',
		}
	},
	methods: {
		toast(type, text) {
			this.type = type
			this.show = true
			this.message = text
		},
		toastpublin(type) {
			if (type) {
				this.$rm.toast.show({
					type,
					text: ''
				})
			} else {
				this.$rm.toast.text('')
			}
		}
	}
}
</script>

<style></style>
